.cards {
  box-sizing: border-box;
  grid-template-rows: repeat(2, 408px);
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
  transition: all .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.card__txt {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__sum {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin-top: 16px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .3s;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  outline: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: flex;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.header__img {
  height: 40px;
}

.nav {
  color: #060b35;
}

.nav__item {
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav__items {
  gap: 20px;
  list-style-type: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  transition: all .3s;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.page__body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, Raleway, serif;
  font-size: 12px;
  font-weight: 500;
}

@media (width >= 488px) {
  .cards {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards {
    grid-template-columns: repeat(4, 200px);
  }
}

a {
  color: inherit;
  text-decoration: none;
}

p, h2 {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

